/**
 * Utilitaire pour grer les erreurs API de manire cohrente
 */

/**
 * Extrait le message d'erreur le plus appropri d'une rponse d'erreur
 * @param {Error|Object} error - L'erreur capture
 * @param {string} defaultMessage - Message par dfaut si aucun message spcifique n'est trouv
 * @returns {string} Le message d'erreur  afficher  l'utilisateur
 */
export function extractErrorMessage(error, defaultMessage = 'Une erreur est survenue') {
    console.error('Erreur API:', error);
    
    // Si l'erreur a un message direct
    if (error.message) {
        return error.message;
    }
    
    // Si l'erreur provient d'une rponse HTTP avec des donnes
    if (error.response && error.response.data) {
        const data = error.response.data;
        
        // Message principal
        if (data.message) {
            return data.message;
        }
        
        // Erreurs de validation Laravel
        if (data.errors) {
            const firstErrorKey = Object.keys(data.errors)[0];
            if (firstErrorKey && data.errors[firstErrorKey][0]) {
                return data.errors[firstErrorKey][0];
            }
        }
        
        // Erreur gnrique
        if (data.error) {
            return data.error;
        }
    }
    
    // Si c'est un objet d'erreur fetch/axios
    if (error.status || error.statusText) {
        return `Erreur ${error.status || ''}: ${error.statusText || 'Erreur de rseau'}`;
    }
    
    return defaultMessage;
}

/**
 * Gre une erreur API et retourne un objet avec le statut et le message
 * @param {Error|Object} error - L'erreur capture
 * @param {string} defaultMessage - Message par dfaut
 * @returns {Object} { success: false, message: string }
 */
export function handleApiError(error, defaultMessage = 'Une erreur est survenue') {
    const message = extractErrorMessage(error, defaultMessage);
    return {
        success: false,
        message: message
    };
}

/**
 * Wrapper pour les appels API qui gre automatiquement les erreurs
 * @param {Function} apiCall - La fonction d'appel API
 * @param {string} defaultErrorMessage - Message d'erreur par dfaut
 * @returns {Promise} Rsultat de l'appel API ou erreur formate
 */
export async function safeApiCall(apiCall, defaultErrorMessage = 'Une erreur est survenue') {
    try {
        const result = await apiCall();
        return result;
    } catch (error) {
        return handleApiError(error, defaultErrorMessage);
    }
}